import React, { Component } from 'react';
import './App.css';
import CalendarDay from './components/calendarDay.js';
import CalendarWeek from './components/calendarWeek.js';
import Calendar from './components/calendar.js';
import moment from 'moment';
import Toggle from './components/toggle.js';
import Listview from './components/ListView.js';


var todayMonth;
var m = moment().get('month');
m = m + 1;

if( m < 10 ) {
  var zero = "0";
  m = zero.concat(m.toString());

  todayMonth = m;
} else {
  todayMonth = m.toString();
}

var todayYear = moment().get('year').toString();
var todayDate = moment().get('date').toString();
if(todayDate < 10){
  todayDate = "0".concat(todayDate);
}
var todayHour = moment().get('hour').toString();
if(todayHour < 10){
  todayHour = "0".concat(todayHour);
}
var todayMinute = moment().get('minutes').toString();
if(todayMinute < 10){
  todayMinute = "0".concat(todayMinute);
}

var today = todayYear + '-' + todayMonth + '-' + todayDate + "T" + todayHour + ":" + todayMinute;


const currentBatch = {
  start: '2018-09-24T13:05',
  end: today,
  matched: '34,123',
  unmatched: '201',
  key: '1231231231'
}

const fifthBatch = {
  start: '2018-09-18T18:05',
  end: '2018-09-24T13:05',
  matched: '34,123',
  unmatched: '201',
  key: '1231231231'
}

const fourBatch = {
  start: '2018-09-10T18:05',
  end: '2018-09-18T18:05',
  matched: '34,123',
  unmatched: '201',
  key: '564302423'
}

const threeBatch = {
  start: '2018-08-28T09:15',
  end: '2018-09-10T18:05',
  matched: '34,123',
  unmatched: '201',
  key: '56430126783'
}

const twoBatch = {
  start: '2018-08-21T07:41',
  end: '2018-08-28T09:15',
  matched: '34,123',
  unmatched: '201',
  key: '12093302423'
}

const oneBatch = {
  start: '2018-08-13T13:17',
  end: '2018-08-21T07:42',
  matched: '34,123',
  unmatched: '201',
  key: '46896402423'
}

const calendarCont = {
  margin: '0px auto'
}


const batches = [
currentBatch,
fifthBatch,
fourBatch,
threeBatch,
twoBatch,
oneBatch
];

const donationCard = {
  backgroundColor: 'white',
  margin: '0px auto 32px auto',
  padding: '32px 32px 48px 32px',
  width: '1200px',
  boxShadow: '0px 4px 12px 0px rgba(0,0,0,0.1)',
  borderRadius: '24px'
}


const donationCardTitle = {
  color: '#5E5E5E',
  fontSize : '32px',
  fontWeight: 'lighter',
  textAlign: 'left',
  margin: '24px 24px 24px 48px',
  width: '500px',
  display: 'inline-block',
  float: 'left',
  position: 'relative'
}

const donationCardTopHolder = {
  display: 'block',
  width: '100%',
  height: '94px'
}

const header = {
  width: '100%',
  height: '100px',
  backgroundColor: 'white',
  marginBottom: '56px'
}


class App extends Component {

  constructor(props) {
    super(props);

    this.state = {
        calendar: true
    }

    this.changeToCalendar = this.changeToCalendar.bind(this);
    this.changeToList = this.changeToList.bind(this);
  }

  changeToCalendar(e){ 
    this.setState({
      calendar: true
    });

  }

  changeToList(e){
    this.setState({
      calendar: false
    });
  }



  render() {
    return (
      <div className="App">
              <div style={header} > </div>

              <div style={donationCard}>
                <div style={donationCardTopHolder} >
                    <h2 style={donationCardTitle} >Donation Export</h2>

                    <Toggle calendarToggle={this.changeToCalendar} listToggle={this.changeToList} />
                </div>
                { (this.state.calendar) ? 
                  <Calendar 
                    data = {batches}
                    style = {calendarCont}
                    now = {today}
                  />
                :
                  <Listview 
                    data = {batches}
                    style = {calendarCont}
                    now = {today}
                  />
                }
            </div> 
 
      </div>
    );
  }
}

export default App;





























