	createMonth(w){

		var earlierDays = [];
		var month = w.month - 1;

		for(var i = 0; i <= 13; i++){
			var firstDay = moment().set({'date': w.firstDay, 'month': month});
			var d = firstDay.add(i,'days');
			var day = {
				date: d.get('date'),
				month: d.get('month')
			}
			earlierDays.push(day);
		}

		var month = earlierDays.concat(w.week);

		return month;
	}

	inRange(d, sMonth, eMonth, sDay, eDay, sYear, eYear ){

		// sMonth = parseInt(sMonth);
		// sMonth = sMonth - 1;
		// eMonth = parseInt(eMonth);
		// eMonth = eMonth - 1;

		var dStart = {
			year: sYear,
			month: sMonth,
			day: sDay
		}	

		var dEnd = {
			year: eYear,
			month: eMonth,
			day: eDay
		}

		var startDate = new Date(dStart.year, dStart.month, dStart.day);
		var endDate = new Date(dEnd.year, dEnd.month, dEnd.day);
		var compareDate = new Date(2018, d.month, d.date);

		// console.log(dEnd.year);
		// console.log(dEnd.month);
		// console.log(dEnd.day);

		// console.log(startDate);
		// console.log(endDate);
		// console.log(compareDate);
		// console.log(" ");

		var range = Moment.range(startDate, endDate);


		return range.contains(compareDate);

	}


	// is there a fill, is current, is end?, is even, attached start & end date, is in a batch?
	compareBatchTime(day){

		var _this = this;
		var batches = this.state.data;
		var dayData = {};

		var _this = this;
		var batches = this.state.data;
		var theMonth = day.month;
		theMonth = theMonth + 1;
		theMonth = theMonth.toString();
		var theDate = day.date;
		theDate = theDate.toString();

		var theDay = {
			date: theDate,
			month: theMonth
		};

		for(var i = 0 ; i < batches.length; i++){

			var startMonth = batches[i].start.slice(5,7);
			startMonth = parseInt(startMonth);
			startMonth = startMonth - 1;
			var endMonth = batches[i].end.slice(5,7);
			endMonth = parseInt(endMonth);
			endMonth = endMonth - 1;
			var startDay = batches[i].start.slice(8,10);
			var endDay = batches[i].end.slice(8,10);
			var startYear = batches[i].start.slice(0,4);
			var endYear = batches[i].end.slice(0,4);

			

			var containsDate = _this.inRange(day, startMonth, endMonth, startDay, endDay, startYear,endYear);
			//hasBatch = Object.assign( hasBatch, { batchFile: batches[i]  } );

			dayData['inBatch'] = containsDate;

			// console.log(_this.today);
			// console.log(batches[i].end);
			// console.log(" ");

			var isCurrent = (_this.today == batches[i].end ) ? true : false;

			if(isCurrent) {

				//console.log("is current is true");
				//console.log(batches[i]);
				_this.setState({
					cardInfo: batches[i]
				});
			}

			if(containsDate){


				var firstBar = {};
				var secondBar = {};

				var theDayDate = parseInt(theDay.date);
				var endDDay = parseInt(endDay);
				var startDDay = parseInt(startDay);

				var hasEnd = (theDayDate == endDDay && day.month == endMonth) ? true : false;
				var hasStart = (theDayDate == startDDay && day.month == startMonth) ? true : false;

				if(hasEnd){


					var isEven = (i % 2 == 0) ? true : false;
					var isCurrent = (_this.today == batches[i].end ) ? true : false;

					var end = {
						isEnd: true,
						endFile : batches[i],
						endHour : batches[i].end.slice(11,13),
						even : isEven,
						current: isCurrent
					}
					dayData['dataEnd'] = end;

				} else if (hasStart){
					var isEven = (i % 2 == 0) ? true : false;
					var isCurrent = (_this.today == batches[i].end ) ? true : false;

					var start = {
						isStart : true,
						startFile : batches[i],
						startHour : batches[i].start.slice(11,13),
						even : isEven,
						current : isCurrent
					}

					dayData['dataStart'] = start;

				} else if (!hasEnd && !hasStart){
					var isEven = (i % 2 == 0) ? true : false;
					var isCurrent = (_this.today == batches[i].end ) ? true : false;

					var fullBar = {
						file: batches[i],
						even: isEven,
						current: isCurrent
					}

					dayData['dataGeneral'] = fullBar;
				}

			}

			if(i == batches.length - 1 ){
				return dayData;
			}


		}

	}





	createWeeks(m){

		
		var firstWeek = [];
		var secondWeek = [];
		var thirdWeek = [];
		var _this = this;

		var d = _this.state.data;



		m.map(function(val, index, arr){

			if(index <= 6){

				var calChar = _this.compareBatchTime(val);

				var dayValue = Object.assign(val, calChar);


				firstWeek.push({v: dayValue, key: index});
			} else if (index <= 13){

				var calChar = _this.compareBatchTime(val);

				var dayValue = Object.assign(val, calChar);

				secondWeek.push({v: dayValue, key: index});
			} else {

				var calChar = _this.compareBatchTime(val);

				var dayValue = Object.assign(val, calChar);

				thirdWeek.push({v: dayValue, key: index});
			}
		});


		var ww = {
			one: firstWeek,
			two: secondWeek,
			three : thirdWeek
		};

		_this.setState({
			weeks: ww
		})

	}

