	componentDidMount(){

		var _this = this;
		this.momentDay = moment();

		var dat = _this.props.data;

		this.setState({
			data: dat
		});


		var weekOne = function() {

			var dayOfWeek = _this.momentDay.day();			
			var currentWeek = _this.getCurrentWeek(dayOfWeek);
			return currentWeek;
		};

		var c = weekOne();

		this.setState({
			currWeek: c
		});

		var todayMonth;
		if( moment().get('month') < 10 ) {
		  var zero = "0";
		  var m = moment().get('month');
		  m = m + 1;
		  m = zero.concat(m.toString());
		  todayMonth = m;
		} else {
		  todayMonth = moment().get('month');
		};

		var todayMonth;
		var m = moment().get('month');
		m = m + 1;

		if( m < 10 ) {
		  var zero = "0";
		  m = zero.concat(m.toString());

		  todayMonth = m;
		} else {
		  todayMonth = m.toString();
		}

		var todayYear = moment().get('year').toString();
		var todayDate = moment().get('date').toString();
		if(todayDate < 10){
		  todayDate = "0".concat(todayDate);
		}
		var todayHour = moment().get('hour').toString();
		if(todayHour < 10){
		  todayHour = "0".concat(todayHour);
		}
		var todayMinute = moment().get('minutes').toString();
		if(todayMinute < 10){
		  todayMinute = "0".concat(todayMinute);
		}

		this.today = todayYear + '-' + todayMonth + '-' + todayDate + "T" + todayHour + ":" + todayMinute;
	
	}

	componentDidUpdate(prevProps, prevState) {

		var _this = this;
		if(prevState.currWeek !== _this.state.currWeek ) {
			var currentWeek = _this.state.currWeek;
			var currentMonth = _this.createMonth(currentWeek);
			

			_this.setState({
				month : currentMonth
			});
		}

		if(prevState.month !== _this.state.month) {
			var m = _this.state.month;
			_this.createWeeks(m);
		}

	}


