import React , {Component} from 'react';



const toggleArea = {
	float: 'right',
	margin: '32px 48px 24px 48px'
}

const toggleStyle = {
	width: '110px',
	height: '30px',
	borderRadius: '32px',
	display: 'inline-block',
	marginLeft: '16px',
	lineHeight: '30px'
}

const activeToggle = {
	backgroundColor: '#E1E3E6',
	color: '#303030',
	fontSize: '12px',
	fontWeight: '600'
}

const inactiveToggle = {
	border: '1px solid #C1C3C6',
	color: '#9B9B9B',
	fontSize: '12px',
	fontWeight: '600'
}




class Toggle extends Component {

	constructor(props){
		super(props);

		this.state = {
			calendar: true
		}

		this.cEvent = this.cEvent.bind(this);
		this.lEvent = this.lEvent.bind(this);
	}

	cEvent(e){
		this.props.calendarToggle(e, 'cal');
		this.setState({
			calendar: true
		});
	}

	lEvent(e) {
		this.props.listToggle(e, 'list');

		this.setState({
			calendar: false
		});
	}

	render() {

		var calendarStyle = (this.state.calendar) ? Object.assign({}, toggleStyle, activeToggle) : Object.assign({}, toggleStyle, inactiveToggle);
		var listStyle = (this.state.calendar) ? Object.assign({}, toggleStyle, inactiveToggle) : Object.assign({}, toggleStyle, activeToggle);

		return (
			<div style={toggleArea} >
				<div style={calendarStyle} className="hoverClass" onClick={this.cEvent} > Calendar </div>

				<div style={listStyle} className="hoverClass" onClick={this.lEvent}  > List </div>
			</div>
		);
	}

}


export default Toggle;






















