'use strict'; 
import React, { Component } from 'react';
import moment from 'moment';

const dayCard = {
	backgroundColor: 'white',
	width: '135px',
	height: '67px',
	border: '1px solid #E1E3E6',
	display: 'inline-block',
	boxSizing: 'border-box'
};

const number = {
	fontWeight: 'normal',
	fontSize: '14px',
	color: '#4A4A4A',
	textAlign: 'left',
	margin: '16px 16px 4px 16px'
};

const fillContainer = {
	height: '22px',
	width: '100%'
};

const fillStart = {

	position: 'absolute',
	height: '22px',
	width: '135px',
	transformOrigin: 'right'
};

const fillEnd = {

	position: 'absolute',
	height: '22px',
	width: '135px',
	transformOrigin: 'left'
};



class CalendarDay extends Component {

	constructor(props) {
		super(props);
		this.state = {
			date: 0,
			fillEnd: fillEnd,
			fillStart: fillStart,
			currentDay: false
		};

		this.selectFill = this.selectFill.bind(this);
		this.fillPercentage = this.fillPercentage.bind(this);
		this.evenFill = this.evenFill.bind(this);
		this.handleChangeEnd = this.handleChangeEnd.bind(this);
		this.handleChangeStart = this.handleChangeStart.bind(this);

	}

	componentDidMount() {
		var _this = this;

		var day = _this.props.day;
		var data = _this.props.data;

		this.setState({
			date: day
		});

		this.selectFill(data);
		

	}

	componentDidUpdate(prevProps, prevState) {

		var _this = this;

		if(prevProps.hNewBatch !== _this.props.hNewBatch){
			_this.setState({
				currentDay: true
			});
		}

		if(prevState.currentDay !== _this.state.currentDay){
			var data = _this.props.data;

			if(data.hasOwnProperty('dataEnd') && data.hasOwnProperty('dataStart')){
				data.dataEnd.current = false;
				data.dataStart.current = false;
			} else if(data.hasOwnProperty('dataEnd')){
				data.dataEnd.current = false;
			} else if (data.hasOwnProperty('dataStart')){
				data.dataStart.current = false;
			} else if (data.hasOwnProperty('dataGeneral')){
				data.dataGeneral.current = false;
			}
			_this.selectFill( data );
		}

	}


	evenFill(even, current){

		var _this = this;

		const fillOdd = {
			backgroundColor: '#00CDFF',
			opacity: '0.75'
		};

		const fillEven = {
			backgroundColor: '#0E91FD',
			opacity: '0.75'
		};

		const fillEmpty = {
			backgroundColor: 'none',
			opacity: '0'
		};

		const fillCurrent = {
			backgroundColor: '#FFA700',
			opacity: '0.8'
		};


		if( even == true && current == false) {

			return fillEven;

		} else if( even == false && current == false ) {


			return fillOdd;				

		} else if( current == true ){


			return fillCurrent;
		} else {
			return fillEmpty;
		}
	}

	fillPercentage(h, side){
		var hour = parseInt(h);
		var percentage = Math.round(hour/24 * 100)/ 100;

		percentage = (side == 'end') ? (Math.round(hour/24 * 100)/ 100) : (1 - (Math.round(hour/24 * 100)/ 100));

		percentage = percentage.toString();

		const scale = {
			transform : 'scaleX(' + percentage + ')'
		}

		return scale;
	}



	selectFill(d) {

		var _this = this;
		var scale;
		var color;


		if(d.hasOwnProperty('dataEnd') && d.hasOwnProperty('dataStart')){

			scale = _this.fillPercentage(d.dataEnd.endHour, 'end');
			color = _this.evenFill(d.dataEnd.even, d.dataEnd.current);
			var newEndFill = Object.assign({}, fillEnd, scale, color);

			scale = _this.fillPercentage(d.dataStart.startHour, 'start');
			color = _this.evenFill(d.dataStart.even, d.dataStart.current);
			var newStartFill = Object.assign({}, fillStart, scale, color);

			_this.setState({
				fillEnd: newEndFill,
				fillStart: newStartFill
			});
		} else if (d.hasOwnProperty('dataStart')){

			scale = _this.startPercentage(d.dataStart.startHour, 'start');
			color = _this.evenFill(d.dataStart.even, d.dataStart.current);
			var newStartFill = Object.assign({}, fillStart, scale, color);

			_this.setState({
				fillStart: newStartFill
			});

		} else if(d.hasOwnProperty('dataEnd')){
			scale = _this.fillPercentage(d.dataEnd.endHour, 'end');
			color = _this.evenFill(d.dataEnd.even, d.dataEnd.current);
			var newEndFill = Object.assign({}, fillEnd, scale, color);

			const newBatchFill = {
				backgroundColor: '#FFA700',
				opacity: '0.2',
				position: 'absolute',
				height: '22px',
				width: '135px',
				transformOrigin: 'right'
			};

			if(_this.state.currentDay){
				_this.setState({
					fillEnd: newEndFill,
					fillStart: newBatchFill
				});		
			} else {
				_this.setState({
					fillEnd: newEndFill
				});
			}




		} else if(d.hasOwnProperty('dataGeneral')){
			scale = {transform: 'scaleX(1)'};
			color = _this.evenFill(d.dataGeneral.even , d.dataGeneral.current);
			var newGeneralFill = Object.assign({}, fillEnd, scale, color);

			scale = {transform: 'scaleX(0)'};
			var newHideFill = Object.assign({}, fillStart, scale);

			_this.setState({
				fillEnd: newGeneralFill,
				fillStart: newHideFill
			});
		}
	}



	handleChangeEnd(e){

		var _this = this;
		var data = _this.props.data;
		var f;
		if(data.hasOwnProperty('dataGeneral')){
			f = data.dataGeneral.file;
		} else if(data.hasOwnProperty('dataEnd')){
			f = data.dataEnd.endFile;
		}

		if(_this.props.onChange) {
			_this.props.onChange(e, f);
		}

	}


	handleChangeStart(e){

		var _this = this;
		var data = _this.props.data;

		var f = data.dataStart.startFile;

		if(_this.props.onChange) {
			_this.props.onChange(e, f);
		}


	}




	render() {

		var _this = this;

		return (
			<div style={dayCard} >
				<p style={number} >{this.state.date}</p>
				<div style={fillContainer} className="hoverClass">
					<div style={ _this.state.fillEnd } onClick={_this.handleChangeEnd}  > </div>
					<div style={ _this.state.fillStart } onClick={_this.handleChangeStart}  > </div>
				</div>

			</div>
		);
	}
}


export default CalendarDay;




















