import React, { Component } from 'react';
import ExportButton from './ExportButton.js';
import moment from 'moment';

const greyCard = {
	backgroundColor: '#F5F5F5',
	width: '1136px',
	minHeight: '620px',
	height: 'auto',
	boxSizing: 'border-box',
	padding: '32px',
	margin: '0px auto',
	borderRadius: '14px'
}

const listRow = {
	width: '1024px',
	height: '108px',
	backgroundColor: '#FFFFFF',
	margin: '0px auto'
}

const listRowOrange = {
	width: '1024px',
	height: '108px',
	backgroundColor: '#FFFFFF',
	margin: '0px auto',
	borderBottom: '4px solid #FFA700'
}


const listRowBlue = {
	width: '1024px',
	height: '108px',
	backgroundColor: '#F5F7FA',
	margin: '0px auto'
}

const listItem = {
	width: '20%',
	display: 'inline-block',
	verticalAlign: 'top',
	height: '108px',
	marginRight: '24px'
}



const listButtonHolder = {
	width: '10%',
	display: 'inline-block',
	float: 'left',
	verticalAlign: 'top',
	height: '108px'
}

const listText = {
	lineHeight: '108px',
	height: '108px',
	color: '#4A4A4A',
	fontSize: '14px',
	fontWeight: '300',
	verticalAlign: 'text-top',
	margin: '0px'
}

const listButton = {
	height: '108px',
	lineHeight: '108px',
	width: '32px',
	border: '1px solid #C1C3C6'
}

const buttonStyle = {
	float: 'right',
	maginBottom: '16px'
}

const buttonContainer = {
	width: '1024px',
	height: '70px',
	margin: '32px auto 32px auto'
}

const rowTitles = {
	width: '1024px',
	height: '50px',
	backgroundColor: '#E1E3E6',
	margin: '0px auto',
	color: '#303030'
}

const rowTitleItems = {
	width: '20%',
	display: 'inline-block',
	verticalAlign: 'top',
	height: '50px',
	marginRight: '24px'
}

const rowTitleText = {
	lineHeight: '50px',
	height: '50px',
	color: '#4A4A4A',
	fontSize: '14px',
	fontWeight: '300',
	verticalAlign: 'text-top',
	margin: '0px'
}

const rowTitleButtonHolder = {
	width: '10%',
	display: 'inline-block',
	float: 'left',
	verticalAlign: 'top',
	height: '50px'
}


const footerText = {
	color: '#8C8C8C',
	fontSize: '12px',
	fontWeight: 'normal',
	textAlign: 'left',
	width: '370px',
	margin: '0px auto',
	verticalAlign: 'top',
	position: 'relative',
	opacity: '0.8',
	float: 'left'
}

class Listview extends Component {

	constructor(props) {
		super(props);

		this.state = {
			list: []
		};

		this.listen = this.listen.bind(this);

	}

	componentDidMount(){
		var data = this.props.data;
		//this.parseData(data);
		this.setState({
			list: data
		});
	}

	listen(){

	}

	render() {
		var _this = this;
		return (
			<div style={greyCard}>
				<div style={buttonContainer} >
					<p style={footerText} >
						The Givelify system is consistenly preparing your donations for export. Upon export the Givelify system will immediately start to prepare the next set of donations. 
					</p>
					<ExportButton style={buttonStyle} onExport={this.listen}/>
				</div>
				<div style={rowTitles} >
					<div style={rowTitleButtonHolder} >
						<p style={rowTitleText} > Select </p>
					</div>
					<div style={rowTitleItems} >
						<p style={rowTitleText} > Start Time </p>
					</div>
					<div style={rowTitleItems}>
						<p style={rowTitleText} > End Time </p>
					</div>
					<div style={rowTitleItems}>
						<p style={rowTitleText} > Matched </p>
					</div>
					<div style={rowTitleItems}>
						<p style={rowTitleText} > Unmatched </p>
					</div>
				</div>
				<div style={listRowOrange} >
					<div style={listButtonHolder} >
						<input type="radio" style={listButton} />
					</div>
					<div style={listItem} >
						<p style={listText} > 2018-09-24T13:05 </p>
					</div>
					<div style={listItem}>
						<p style={listText} > Currently In Progress </p>
					</div>
					<div style={listItem}>
						<p style={listText} > 4,123 </p>
					</div>
					<div style={listItem}>
						<p style={listText} > 58 </p>
					</div>
				</div>

				{
					_this.state.list.map((v, i, arr) => (
						<div style={(i % 2 == 0) ? listRow : listRowBlue} >
							<div style={listButtonHolder} >
								<input type="radio" style={listButton} />
							</div>
							<div style={listItem} >
								<p style={listText} > {v.start} </p>
							</div>
							<div style={listItem}>
								<p style={listText} > {v.end} </p>
							</div>
							<div style={listItem}>
								<p style={listText} > {v.matched} </p>
							</div>
							<div style={listItem}>
								<p style={listText} > {v.unmatched} </p>
							</div>
						</div>
					))
				}

			</div>
		);	
	}

}

export default Listview;









