import React, { Component } from 'react';


const buttonStyle = {
	width: '382px',
	height: '69px',
	display: 'inline-block',
	backgroundColor: '#106FF7',
	color: 'white',
	borderRadius: '9px',
	float: 'right'
};

const buttonStylePressed = {
	width: '382px',
	height: '69px',
	display: 'inline-block',
	backgroundColor: '#1D55A6',
	color: 'white',
	borderRadius: '9px',
	opacity : '0.8',
	float: 'right'
};

const textStyle = {
	verticalAlign: '',
	textAlign: 'center',
	margin: '0px',
	height: '69px',
	lineHeight: '69px'
}


class ExportButton extends Component {

	constructor(props){
		super(props);

		this.state = {
			pressed: false
		}

		this.handleEvent = this.handleEvent.bind(this);
	}

	handleEvent(e) {
		this.props.onExport(e);

		this.setState({
			pressed: true
		});

	}

	render() {
		var _this = this;

		return (
			<div style={(_this.state.pressed) ? buttonStylePressed : buttonStyle} onClick={_this.handleEvent} className="hoverClass" >
				<p style={textStyle} >
					Export File for ACS
				</p>
			</div>
		);
	}

}

export default ExportButton;


