import React, { Component } from 'react';
import ExportButton from './ExportButton.js';
import moment from 'moment';


const infoMod = {
	display: 'inline-block',
	width: '88px',
	float: 'left',
	margin: '0px 48px 0px 0px'
}

const smallTitle = {
	fontWeight: 'bold',
	fontSize: '14px',
	color: '#4A4A4A',
	margin: '0px',
	textAlign: 'left'
}

const bodyType = {
	fontWeight: 'regular',
	fontSize: '16px',
	color: '#4A4A4A',
	margin: '8px 0px 0px 0px',
	textAlign: 'left',
	opacity: '0.7'
}

const calCard = {
	width: '1034px',
	height: '324px',
	margin: '0px auto',
	backgroundColor: 'white',
	borderRadius: '14px',
	transform: 'translate(0px,-166px)',
	zIndex: '1',
	position: 'relative'
}

const calCardInfoArea = {
	width: '940px',
	height: '160px',
	margin: '32px auto 56px auto',
	paddingTop: '166px'
}

const buttonStyle = {
	float: 'right'
}

const alertArea = {
	display: 'inline-block',
	width: '520px',
	height: '70px',
	backgroundColor: '#E9F7DB',
	border: '1px solid #B8E986',
	float: 'left',
	verticalAlign: 'top',
	borderRadius: '4px',
	marginRight: '24px'
}

const alertText = {
	textAlign: 'left',
	verticalAlign: 'center',
	fontWeight: 'lighter',
	color: '#508F51',
	fontSize: '12px',
	height: '38px',
	lineHeight: '14px',
	margin: '20px 16px 16px 16px',
	display: 'inline-block',
	width: '300px',
	float: 'left'
}

const emailButtonStyle = {
	width: '114px',
	height: '27px',
	display: 'inline-block',
	backgroundColor: '#106FF7',
	color: 'white',
	borderRadius: '4px',
	float: 'right',
	margin: '20px 16px 16px 16px'
};

const emailTextStyle = {
	verticalAlign: '',
	textAlign: 'center',
	margin: '0px',
	height: '27px',
	lineHeight: '27px',
	fontSize: '12px',
	fontWeight: 'lighter'
}



class InfoMod extends Component {

	constructor(props) {
		super(props);
		this.state = {

		};
	}

	render(){

		var _this = this;

		return (
			<div style={infoMod} >
				<h4 style={smallTitle} > {_this.props.Title} </h4>
				<p style={bodyType} > {_this.props.Body} </p>
			</div>
		);
	}

}


class CalendarCard extends Component {

	constructor(props) {
		super(props);
		this.state = {
			firstTitle: "Start Date",
			firstBody: "",
			secondTitle: "End Date",
			secondBody: "",
			thirdTitle: "Matched",
			thirdBody: "",
			fourthTitle: "Unmatched",
			fourthBody: "",
			cardArea: 1
		};

		this.createBodyText = this.createBodyText.bind(this);
		this.onClickEvent = this.onClickEvent.bind(this);	
	}


	createBodyText(s, e, m, u){

		var _this = this;
		var end = e;

		var thisMonth = moment().get('month');
		thisMonth = thisMonth + 1;
		var thisDay = moment().get('date');

		var cardMonth = e.slice(5,7);
		cardMonth = parseInt(cardMonth);
		var cardDay = e.slice(8,10);
		cardDay = parseInt(cardDay);

		if(thisDay == cardDay && thisMonth == cardMonth) {
			end = "Currently In Progress"
		}


		_this.setState({
			firstBody: s,
			secondBody: end,
			thirdBody: m,
			fourthBody: u
		});

	}


	componentDidMount(){

		var _this = this;

		var data = _this.props.info;

	}

	componentDidUpdate(prevProps, prevState){

		var _this = this;

		var data = _this.props.info;

		if(prevProps.info !== _this.props.info) {
			_this.createBodyText(data.start,data.end, data.matched, data.unmatched);
		}
	}

	onClickEvent(e){
		this.props.onExportClick(e);
		this.setState({
			cardArea: 2
		});

	}	

	render(){

		var _this = this;
		return (
			<div style={calCard} >
				<div style={calCardInfoArea}>
					{
						(this.state.cardArea == 1) ?
						<div>
							<InfoMod 
								Title={this.state.firstTitle}
								Body={this.state.firstBody}
							/>
							<InfoMod 
								Title={this.state.secondTitle}
								Body={this.state.secondBody}
							/>

							<InfoMod 
								Title={this.state.thirdTitle}
								Body={this.state.thirdBody}
							/>

							<InfoMod 
								Title={this.state.fourthTitle}
								Body={this.state.fourthBody}
							/>

						</div>
						:
						<div style={alertArea} >
							<p style={alertText}>
								An email has been sent to your email address with the requested ACS Compatible File(s)
							</p>
							<div style={emailButtonStyle} onClick={_this.handleEvent} >
								<p style={emailTextStyle} >
									Send Email
								</p>
							</div>
						</div>
					}

			
					<ExportButton
						style={buttonStyle}
						onExport = {this.onClickEvent}
					/>	

				</div>
			</div>
		);
	}



}

export default CalendarCard;





























