import React, { Component } from 'react';
import CalendarDay from './calendarDay.js';


class CalendarWeek extends Component {

	constructor(props) {
		super(props);

		var theDate = new Date(),
			today = theDate.getDate();

		this.state = {
			today: today,
			week: [],
			exportPressed: false
		};

		this.handleSelection = this.handleSelection.bind(this);


	}

	componentDidMount(){
		var _this = this;
		var w = _this.props.data;


		if(w !== undefined) {
			_this.setState({
				week: w
			});
		}


	}

	componentWillUpdate(nextProps, nextState){
		var _this = this;

		if(nextProps.data !== _this.props.data ){
			
			var w = nextProps.data;

			_this.setState({
				week: w
			});
		}

		if(nextProps.startNewBatch !== _this.props.startNewBatch){
			_this.setState({
				exportPressed: true
			});
		}

	}


	handleSelection(ev, file){

		this.props.selectChange(ev,file);
	}


	render() {
		var _this = this;

		return (
			<div>
				 {
					_this.state.week.map((val, index, arr) => (
						<CalendarDay 
							day={val.v.date}
							data={val.v}
							key={index}
							onChange={this.handleSelection}
							sNewBatch={this.props.startNewBatch}
							hNewBatch={this.state.exportPressed}
						/>
					))
				 }
			</div>
		);
	}

}

export default CalendarWeek;


